<template>
  <div id="edit-recipe">
    <h3>Edit Recipe</h3>
    <div class="row">
        <form @submit.prevent="updateRecipe" class="col s12">
          <div class="row">
            <div class="input-field col s12">
              <input disabled type="text" v-model="recipe_id" required>
              <!-- <label>Recipe ID#</label> -->
            </div>    
          </div>
          <div class="row">
            <div class="input-field col s12">
              <input type="text" v-model="name" required>
              <!-- <label>Name</label> -->
            </div>    
          </div>
          <div class="row">
            <div class="input-field col s12">
              <input type="text" v-model="description" required>
              <!-- <label>Description</label> -->
            </div>    
          </div>
          <button type="submit" class="btn">Submit</button>
          <router-link to="/" class="btn grey">Cancel</router-link>   
        </form>
      </div>  
  </div> 
</template>

<<script>
import db from './firebaseInit'
export default {
  name: 'edit-recipe',
  data () {
    return {
      recipe_id: null,
      name: null,
      description: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('recipes').where('recipe_id','==', to.params.recipe_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.recipe_id = doc.data().recipe_id
            vm.name = doc.data().name
            vm.description = doc.data().description
          })
        });
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData() {
      db.collection('recipes').where('recipe_id','==', this.$route.params.recipe_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.recipe_id = doc.data().recipe_id
            this.name = doc.data().name
            this.description = doc.data().description
          })
      })
    },
    updateRecipe () {
      db.collection('recipes').where('recipe_id','==', this.$route.params.recipe_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.update({
              recipe_id: this.recipe_id,
              name: this.name,
              description: this.description
            })
            .then(() => {
              this.$router.push({name: 'view-recipe', params: {recipe_id: this.recipe_id}})
            })
          })
      })
    }
  }
}
</script>


