<template>
  <div id="view-recipe">
    <ul class="collection with-header">
      <li class="collection-header"><h4>{{name}}</h4></li>
      <li class="collection-item">{{description}}</li>
    </ul>
    <router-link to="/" class="btn grey">Back</router-link>
    <button @click="deleteRecipe" class="btn red">Delete</button>
    <div class="fixed-action-btn">
      <router-link v-bind:to="{name: 'edit-recipe', params: {recipe_id:recipe_id}}" class="btn-floating btn-large red">
        <i class="fa fa-pencil"></i>
      </router-link>  
    </div>  
  </div> 
</template>

<<script>
import db from './firebaseInit'
export default {
  name: 'view-recipe',
  data () {
    return {
      recipe_id: null,
      name: null,
      description: null  
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('recipes').where('recipe_id','==', to.params.recipe_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.recipe_id = doc.data().recipe_id
            vm.name = doc.data().name
            vm.description = doc.data().description
          })
        });
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData() {
      db.collection('recipes').where('recipe_id','==', this.$route.params.recipe_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.recipe_id = doc.data().recipe_id
            this.name = doc.data().name
            this.description = doc.data().description
          })
      })
    },
    deleteRecipe () {
      if (confirm('Are you sure?')) {
        db.collection('recipes').where('recipe_id','==', this.$route.params.recipe_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.delete()
            this.$router.push('/')
          })
      })
      }
    }
  }
}
</script>


