<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header"><h4>Recipes</h4></li>
      <li v-for="recipe in recipes" v-bind:key="recipe.id" class="collection-item">
        <div class="chip">{{recipe.name}}</div>{{recipe.description}}
        <router-link class="secondary-content" v-bind:to="{name: 'view-recipe', params: {recipe_id: recipe.recipe_id}}"><i class="fa fa-eye"></i></router-link>
      </li>
    </ul>

    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>  
    </div>    
  </div> 
</template>

<<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data () {
    return {
      recipes: []
    }
  },
  created () {
    db.collection('recipes').orderBy('name').get().then(
      querySnapshot => {
        querySnapshot.forEach(doc => {
          console.log(doc.data());
          const data = {
            'id': doc.id,
            'recipe_id': doc.data().recipe_id,
            'name': doc.data().name,
            'description': doc.data().description
          }
          this.recipes.push(data)
        });
      }
    )
  }
}
</script>




